<template>
  <div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <div class="top_banner">
      <p>
        <span>行业资讯</span>
        了解最新行业动态</p>
    </div>
    <div class="content2">
    <div class="hm_product_num">
      <Tabs value="name1" style="width: 100%;">
        <TabPane label="商标知识" name="name1" @click="getsbknow()">
          <p class="nomore" v-if="sbqg_list.length===0">暂无数据</p>
          <ul v-if="sbqg_list.length !==0">
            <li class="item" @click='new_details(item.id)' v-for="(item, name, index) in sbqg_list" :key="index">
              <p class="title">{{item.name}}</p>
              <span><p class="small_bh">浏览：{{item.total}}人</p>
									<p class="price">发布日期：{{item.createTime}}</p>
									</span>
            </li>
          </ul>
        </TabPane>

        <TabPane label="商标问问" name="name2" style="width: 100%;" @click="getsbknow2()">
          <p class="nomore" v-if="sbqg_list2.length===0">暂无数据</p>
          <ul v-if="sbqg_list.length !==0">
            <li class="item" @click='new_details(item.id)' v-for="(item, name, index) in sbqg_list2" :key="index">
              <p class="title">{{item.name}}</p>
              <span><p class="small_bh">浏览：{{item.total}}人</p>
									<p class="price">发布日期：{{item.createTime}}</p>
									</span>
            </li>
          </ul>
        </TabPane>
        <TabPane label="商标法律" name="name3" style="width: 100%;" @click="getsbknow3()">
          <p class="nomore" v-if="sbqg_list3.length===0">暂无数据</p>
          <ul v-if="sbqg_list.length !==0">
            <li class="item" @click='new_details(item.id)' v-for="(item, name, index) in sbqg_list3" :key="index">
              <p class="title">{{item.name}}</p>
              <span><p class="small_bh">浏览：{{item.total}}人</p>
									<p class="price">发布日期：{{item.createTime}}</p>
									</span>
            </li>
          </ul>
        </TabPane>
      </Tabs>
    </div>
    </div>
</div>
</template>

<script>
  import topMeau from './../public/top_meau.vue';
  import topHeader from './../public/top.vue';
  import { servicegetList } from '../../api/api';
    export default {
        name: "index",
      data() {
        return {
          title:'行业资讯',
          sbqg_list: [],
          sbqg_list2: [],
          sbqg_list3: [],
        }
      },
      components: {
        topMeau,topHeader
      },
      methods: {
        //获取商标知识信息
        getsbknow() {
          let para = {
            type: '2',
          };
          this.listLoading = true;
          //NProgress.start();
          servicegetList(para).then((res) => {
            this.sbqg_list = res.data.result.result;
            this.total = res.data.result.total;
            this.listLoading = false;
            //NProgress.done();
          }).catch((err) => {
            //console.log(err);
          });
        },
        getsbknow2() {
          let para = {
            type: '3',
          };
          this.listLoading = true;
          //NProgress.start();
          servicegetList(para).then((res) => {
            this.sbqg_list2 = res.data.result.result;
            this.total = res.data.result.total;
            this.listLoading = false;
          }).catch((err) => {
            //console.log(err);
          });
        },
        getsbknow3() {
          let para = {
            type:'4',
          };
          this.listLoading = true;
          //NProgress.start();
          servicegetList(para).then((res) => {
            this.sbqg_list3 = res.data.result.result;
            this.total = res.data.result.total;
            this.listLoading = false;
            //NProgress.done();
          }).catch((err) => {
            //console.log(err);
          });
        },
        new_details(id) {
          this.$router.push({
            path: '/new_details',
            query: {
              id: id,
              type: '2',
            }
          })
        },
      },
  mounted() {
    this.getsbknow();
    this.getsbknow2();
    this.getsbknow3();
  }
    }
</script>

<style  lang="less" scoped>
  .hm_product_num {
    width: 1200px;
    margin: 0 auto;
    display: block;
    height: auto !important;
    min-height: 20px;
    overflow: hidden;
    li {
      flex: 2;
      font-size: 18px;
      color: #000000;
      width: 100%;
      line-height: 22px;
      text-align: left;
      padding: 5px 0 15px 0;
      margin-bottom: 20px;
      border-bottom: 1px solid #E6E6E6;
      font-weight: bold;
      span {
        width: 100%;
        display: block;
        width: 100%;
        display: flex;
        justify-content: space-between;
        line-height: 36px;
        font-size: 15px;
        color: #666666;
        font-weight: normal;
        .small_bh {
          text-align: left;
          flex: 1;
          align-items: flex-start;
        }
        .price {
          text-align: right;
          flex: 1;
          align-items: flex-end;
        }
      }
    }
  p {
    font-size: 18px;
    color: #000000;
    text-align: left;
  img {
    height: 76px;
    width: 76px;
    display: block;
    margin: 0 auto 5px auto;
  }
  }
  }
</style>



