<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <div class="top_banner">
      <p >{{name}}
        <span>{{title}}</span>
        {{slogin}}</p>
    </div>
		<Content class="content2 top_p bottom_p" style="margin-bottom: 0px;">
			<div class="hm_product_num">
        <Row type="flex" :gutter="16">
          <Col span="6"   v-for="(item, name, index) in zl_fl" :key="index">
            <p @click="zl_meau(item.categoryFir,item.value)">
              <span  class="sty1_box" :class="item.class">
                <img :src=item.src />
              </span>{{item.name}}</p>
          </Col>
        </Row>
			</div>
		</Content>
	</div>
</template>

<script>
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'product_list',
		data() {
			return {
        site:this.$route.query.site,
        id:this.$route.query.id,
				  name:'',
          title:'',
          slogin:'',
				zl_fl: [
				{
						categoryFir: '生活需要',
						value:'生活需要',
						name: '生活需要',
						src: require('../../assets/shxy.png'),
            class: 'sty4'
					},
					{
						categoryFir: '作业运输',
						value:'作业运输',
						name: '作业运输',
						src: require('../../assets/zyys.png'),
            class: 'sty2'
					},
					{
						categoryFir: '化学冶金',
						value:'化学冶金',
						name: '化学冶金',
						src: require('../../assets/hxyj.png'),
            class: 'sty3'
					},
					{
						categoryFir: '纺织造纸',
						value:'纺织造纸',
						name: '纺织造纸.',
						src: require('../../assets/fzzz.png'),
            class: 'sty1'
					},
					{
						categoryFir: '固定建筑',
						value:'固定建筑',
						name: '固定建筑',
						src: require('../../assets/gdjz.png'),
            class: 'sty4'
					},
					{
						categoryFir: '机械工程',
						value:'机械工程',
						name: '机械工程',
						src: require('../../assets/jxgc.png'),
            class: 'sty2'
					},
					{
						categoryFir: '物理',
						value:'物理',
						name: '物理',
						src: require('../../assets/wul.png'),
            class: 'sty4'
					},
					{
						categoryFir: '电学',
						value:'电学',
						name: '电学',
						src: require('../../assets/dianx.png'),
            class: 'sty3'
					},
				]
			}
		},
		components: {
      topHeader,topMeau
		},
    mounted() {
      this.name='专利库'
      this.title='品类丰富'
    },
		methods: {
      open() {
        const h = this.$createElement;
        this.$msgbox({
          title: '消息',
          message: h('p', null, [
            h('span', null, '内容可以是 '),
            h('i', { style: 'color: teal' }, 'VNode')
          ]),
          showCancelButton: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '执行中...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
			zl_meau(categoryFir,value) {
				this.$router.push({
					path: '/zl_meau',
                    query:{
                    	categoryFir:categoryFir,
                    applyType:'2',
                    value:value,
                    areaCode:this.$route.query.site,
                    }
				})
			},

			sb_all() {
				this.$router.push({
					path: '/sb_all'
				})
			},
			sb_all_mb() {
				this.$router.push({
					path: '/sb_all',
					query:{
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    }
				})
			},
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 1200px;
    margin: 0 auto;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
    .sty1{
      background-color: #52b998;
    }
    .sty2{
      background-color: #1969f6;
    }
    .sty3{
      background-color: #5f42e2;
    }
    .sty4{
      background-color: #ee5b36;
    }
    .sty1_box{
      height: 130px;
      line-height: 130px;
      margin: 0 10% 15px 10%;
      width: 80%;
      align-items: center;
      display: flex;
      border-radius: 8px;
    }
		p {
			font-size: 16px;
			color: #000000;
			text-align: center;
			margin: 16px 0 26px 0;
			img {
				height: 76px;
				width: 76px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
	}
</style>



