<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <div class="top_banner">
      <p >{{name}}
        <span>{{title}}</span>
      </p>
    </div>
		<Content class="content6" :model="service">
      <div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
          <Col span="9">
            <p style="font-weight: bold">{{service.name}}</p>
          </Col>
					<Col span="6">
					<p>价格: <span class="cl_num">{{service.price}}</span></p>
					</Col>
					<Col span="5">
					<p>成交记录:{{service.total}}</p>
					</Col>
          <Col span="4">
            <buyservice></buyservice>
          </Col>

				</Row>
        <img class="bg_box" :src=service.bannerUrl />
				<Row type="flex" :gutter="16" class="de_cont  bottom_p">
					<Col span="24">
					<p class="cont" v-html='service.content' ></p>
					</Col>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
	import { servicegetList, servicecreate, serviceupdate, servicedelete, servicegetById } from '../../api/api';
	import buyservice from './../public/buyservice.vue';
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'service_details',
		data() {
			return {
				name:'',
        title:'',
				price:'',
				total:'',
				content: '',
				bannerUrl:'',
				service:[],
        ids:this.$route.query.id
			}
		},
		components: {
			buyservice,
      topHeader,
      topMeau
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品类别列表
			getservice() {
				let para = {
					id:this.$route.query.id
				};
				this.listLoading = true;
				servicegetById(para).then((res) => {
					this.service = res.data.result;
					this.price=this.service.price;
					this.$route.query.price=this.price;
					this.listLoading = false;
				});
			},
		},
		props: {},
		mounted() {
			this.getservice();
			console.log(this.ids)
      if(this.ids=='38'){
        this.name='商标交易'
        this.title='品类丰富'
      } else if(this.ids=='55') {
        this.name='专利交易'
        this.title='品类丰富'
      }
		}
	}
</script>

<style lang="less" scoped>
	.bg_box {
		width: 100%;
		height: 100%;
		display: block;
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 122px;
		font-weight: bolder;
	}

	.top {
		margin-top: 152px;
	}

	.hm_product_num {
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
	    .de_cont {
			padding:15px 20px 25px 20px;
		}
		.cl_num{
			color: #2B2EF5;
			font-size: 26px;
		}
		span {
			display: inline-block;
			text-align: left;
			font-weight: bold;
		}
		p {
			font-size: 22px;
			color: #000000;
			line-height: 22px;
			width: 50%;
			text-align: left;
			padding: 7px;
		}
		.cont {
			font-size: 14px;
			color: #000000;
			width: 100%;
			line-height: 25px;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
	}
</style>



