<template>
  <div>
  <Content style="width: 100%; background-color:#ffffff">
    <Header class="header">
    <span>
      <p  style="width: 100%; height: 80px; line-height: 80px">
        <router-link to="/"><img  class="logo" src="../../assets/logo.png"/></router-link>
        <span class="top_phone">
        <Phone></Phone>
        </span>
      </p>
				<Row type="flex" style="text-align: left">
          <Col v-for="(item, key, index) in headlist" :key="key" :index="index" :span=item.width>
            <router-link tag='a' :to="item.src"  :class="item.active===1?'active':''">{{item.name}}<p v-if="item.small!==''" class="title-ys">{{item.small}}</p></router-link>
          </Col>
        </Row>
    </span>
    </Header>
  </Content>
  </div>
</template>
<script>
  import Phone from './phone.vue';
  export default {
    name: 'topHeader',
    data() {
      return {
          headlist:[
            {src: '/', active: 0, name: '中商首页', small:'',width:'2'},
            {src: '/product', active: 0, name: '商标专场', small:'new',width:'3'},
            {src: '/productzl', active: 0, name: '专利专场', small:'new',width:'3'},
            {src: '/service_details?id=38&name=商标转让&applyType=0', active: 0, name: '商标转让', small:'hot',width:'3'},
            {src: '/service_details?id=55&name=专利申请&applyType=3', active: 0, name: '专利申请', small:'hot',width:'3'},
            {src: '/service', active: 0, name: '国内服务', small:'new',width:'3'},
            {src: '/gj_service', active: 0, name: '国际服务', small:'hot',width:'3'},
            {src: '/news', active: 0, name: '行业资讯', small:'',width:'2'},
            {src: '/company', active: 0, name: '中商简介', small:'',width:'2'}
            ]
      }
    },
    components: {
      Phone
    },
    methods: {
    }
  }
  </script>
<style lang="less" scoped>
    .top_phone{
      width: 400px;
      display: block;
      float: right;
      top: 72px;
      position: absolute;
      right: 126px
    }
      .active{
        color: #0000FF;
      }
  .title-ys{
    display: block;
    height: 12px;
    line-height: 12px;
    padding: 0 1px;
    background-color: #E4363A;
    color: #ffffff;
    text-align: center;
    font-size: 10px;
    position: absolute;
    top: 23px;
    right: 45px;
  }
  </style>



