<template>
	<div id="app">
		<Customer_service></Customer_service>
		<router-view  :key="$route.fullPath" />
    <fooTer></fooTer>
	</div>
</template>

<script>
	import Customer_service from './components/public/Customer_service.vue';
  import fooTer from './components/public/footer.vue';
  import topMeau from './components/public/top_meau.vue';
	export default {
		name: 'App',
		components: {
      fooTer,
      topMeau,
			Customer_service
		},
	}
</script>

<style>
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
	}
</style>



