<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <div class="top_banner">
      <p >
        <span >{{categoryFir}}·{{categorySec}}</span></p>
    </div>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
        <p class="nomore" v-if="product_list.length === 0">目录下暂无数据</p>
        <Row type="flex" v-if="product_list.length !== 0"  :gutter="24" class="box_ul">
          <Col span="8"   class="item" v-for="(item, name, index) in product_list"  :key="index">
						<img @click='sb_details(item.id,item.name,item.price)' :src="item.bannerUrl" />
						<span @click='sb_details(item.id,item.name,item.price)'><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
					</Col>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
	import { patentgetList } from '../../api/api';
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'zl_list',
		data() {
			return {
				type:'',
				product_list: [],
				categoryFir:this.$route.query.type,
				categorySec:this.$route.query.categorySec,
			}
		},
		components: {
      topHeader,
      topMeau},
		props: {},
		methods: {
			//获取产品列表
			getProducts() {
				let para = {
				categoryFir:this.$route.query.categoryFir,
				categorySec:this.$route.query.categorySec,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.product_list = res.data.result.result;
					// console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					// console.log(err);
				});
			},
			prev() {
				this.$router.go(-1)
			},
			sb_details(id,price,name) {
				this.$router.push({
					path: '/zl_details',
					query:{
						id:id,
						name:name,
						price:price,
				        applyType:this.$route.query.applyType,
					}
				})
			}

		},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		display: flex;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
    .nomore{
      width: 100%;
      text-align: center;
      padding: 30px 0;
      font-size: 20px;
    }
    .box_ul {
      width: 100%;
      margin-top: -16px;
      .item {
        padding: 25px 0;
        display: block;
        height: auto !important;
        min-height: 20px;
        overflow: hidden;
        &:last-child{
          border: none;
        }
        button {
          background: #F4F4F4;
          border: 1px solid #E5E5E5;
          border-radius: 23px;
          width: 47%;
          height: 33px;
          line-height: 21px;
          font-size: 16px;
          float: left;
          margin-top: 15px;
          color: #666;
          margin: 15px 2% 0px 1%;
        }
        button:hover {
          color: #FFFFFF;
          background: #2B2EF5;
          border: none;
        }
      }
      img {
        height: 180px;
        width: 100%;
        border-radius: 3px;
        display: flex;
        float: left;
        margin-bottom: 15px;
        border: 1px solid #f6f6f6;
      }
      span {
        display: inline-block;
        float: left;
        font-size: 16px;
        color: #000000;
        text-align: left;
        padding:0px 20px;
        width: 100%;
        .title {
          font-size: 20px;
          color: #000000;
          font-weight: bold;
        }
        .small_bh {
          font-size: 20px;
          color: #9B9B9B;
          float: right;
          display: inline-block;
        }
        .price {
          font-size: 18px;
          color: #2B2EF5;
          font-weight: bold;
          float: left;
          display: inline-block;
        }
      }
    }
	}
</style>



