<template>
	<div class="layout">
    <topMeau></topMeau>
    <div class="top_banner">
      <p >
        <span >订单管理</span></p>
    </div>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
				<Tabs>
					<TabPane label="商标类" name="name1">
            <Row type="flex" :gutter="24" class="box_ul">
              <Col span="8"   class="item" v-for="(item, name, index) in product_list"  :key="index">
								<span>
									<p class="title">{{item.targetJson.name}}</p>
									<p  v-if='item.score===0' style=" float:right;" class="small_bh">状态:被取消</p>
									<p  v-else-if='item.score===1' style=" float:right;" class="small_bh">状态:待审核</p>
									<p  v-else-if='item.score===2' style=" float:right;" class="small_bh">状态:已审核</p>
									<p class="small_bh">编号:{{item.targetJson.id}}</p>
									<p class="price">¥{{item.amount}}</p></span>
								<span style="width:100%; float:right;">
								<Button type="primary" shape="circle" @click="modal31 = true" v-if='item.score===0 || item.score===2'> 删除订单</Button>
								<Button type="primary" shape="circle" @click="modal21 = true" v-else='item.score===1'>取消订单</Button>
								<Modal v-model="modal21" @on-ok="ok1(item.targetJson.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认删除订单？</p>
								</Modal>
								<Modal v-model="modal31" @on-ok="ok12(item.targetJson.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认取消订单？</p>
								</Modal>
								</span>
							</Col>
						</Row>
					</TabPane>
					<TabPane label="专利类" name="name2" span="8">
            <Row type="flex" :gutter="24" class="box_ul">
              <Col span="8"   class="item" v-for="(item, name, index) in product_list2"  :key="index">
								<span><p class="title">{{item.targetJson.name}}</p>
									<p  v-if='item.score===0' style="float:right;" class="small_bh">状态:被取消</p>
									<p  v-else-if='item.score===1' style="float:right;" class="small_bh">状态:待审核</p>
									<p  v-else-if='item.score===2' style="float:right;" class="small_bh">状态:已审核</p>
									<p  class="small_bh">编号:{{item.targetJson.id}}</p>
									<p class="price">¥{{item.amount}}</p></span>
								<span style="width:100%; float: right;">
								<Button type="primary" shape="circle" @click="modal32 = true" v-if='item.score===0 || item.score===2'> 删除订单</Button>
								<Button type="primary" shape="circle" @click="modal22= true" v-else='item.score===1'>取消订单</Button>
								<Modal v-model="modal22" @on-ok="ok(item.targetJson.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认删除订单？</p>
								</Modal>
								<Modal v-model="modal32" @on-ok="ok2(item.targetJson.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认取消订单？</p>
								</Modal>
								</span>
							</Col>
						</Row>
					</TabPane>
					<TabPane label="服务类" name="name3" span="8">
            <Row type="flex" :gutter="24" class="box_ul">
              <Col span="8"   class="item" v-for="(item, name, index) in product_list3"  :key="index">
								<span><p class="title">{{item.description}}</p>
									<p  v-if='item.score===0' style="float:right;" class="small_bh">状态:被取消</p>
									<p  v-else-if='item.score===1' style="float:right;" class="small_bh">状态:待审核</p>
									<p  v-else-if='item.score===2' style="float:right;" class="small_bh">状态:已审核</p>
									<p  class="small_bh">编号:{{item.id}}</p>
									<p class="price">¥{{item.amount}}</p></span>
								<span style="width:100%; float: right;">
								<Button type="primary" shape="circle" @click="modal33 = true" v-if='item.score===0 || item.score===2'> 删除订单</Button>
								<Button type="primary" shape="circle" @click="modal23= true" v-else='item.score===1'>取消订单</Button>
								<Modal v-model="modal23" @on-ok="ok(item.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认删除订单？</p>
								</Modal>
								<Modal v-model="modal33" @on-ok="ok2(item.id)" @on-cancel="cancel">
				                  <p class="tkwz">确认取消订单？</p>
								</Modal>
								</span>
							</Col>
						</Row>
					</TabPane>
				</Tabs>

			</div>
		</Content>
	</div>
</template>
<script>
  import topMeau from './../public/top_meau.vue';
	import { applylist, applydelete, applyupdate } from '../../api/api';
	export default {
		name: 'zlgl',
		data() {
			return {
				modal21: false,
				modal31: false,
				modal22: false,
				modal32: false,
				modal23: false,
				modal33: false,
				product_list: [],
				product_list2: [],
				product_list3: []

			}
		},
		components: {
      topMeau
		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			}, //获取商标列表
			getProducts1() {
				let para = {
					customerId: sessionStorage.customerId,
					applyType: 1,
					pageSize: 30
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			//获取专利列表
			getProducts2() {
				let para = {
					customerId: sessionStorage.customerId,
					applyType: 2,
					pageSize: 30
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.total = res.data.result.total;
					this.reslist = res.data.result.result;
					this.product_list2 = res.data.result.result;
					//console.log(this.product_list2);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			//获取服务列表
			getProducts3() {
				let para = {
					customerId: sessionStorage.customerId,
					applyType: 0,
					pageSize: 30
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list3 = res.data.result.result;
					//console.log(this.product_list3);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			prev() {
				this.$router.go(-1)
			},
			ok12(id) {
				let para = {
					customerId: sessionStorage.customerId,
					id: id,
					targetJson: {
						status: '3'
					},
					applyType: 1,
				};
				applyupdate(para).then((res) => {
					this.$Message.success('操作成功');
					this.getProducts1()
					//NProgress.done();
				}).catch((err) => {
					this.$Message.warning('err');
				});
			},
			ok(id) {
				let para = {
					customerId: sessionStorage.customerId,
					id: id,
					applyType: 2,
				};
				//NProgress.start();
				applydelete(para).then((res) => {
					this.$Message.success('操作成功');
					this.getProducts2()
					//NProgress.done();
				}).catch((err) => {
					this.$Message.warning('err');
				});
			},
			ok2(id) {
				let para = {
					customerId: sessionStorage.customerId,
					id: id,
					applyType: 2,
				};
				applydelete(para).then((res) => {
					this.$Message.success('操作成功');
					this.getProducts1()
					this.getProducts2()
					this.getProducts3()
					//NProgress.done();
				}).catch((err) => {
					this.$Message.warning('err');
				});
			},
			cancel() {
				this.$Message.warning('取消操作');
			},
		},
		props: {},
		mounted() {
			this.onload();
			this.getProducts2();
			this.getProducts1();
			this.getProducts3()
		}
	}
</script>

<style lang="less" scoped>
	.title_right {
		display: inline-block;
		float: right;
	}

	.hm_product_num {
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;

    .box_ul {
      width: 100%;
      margin-top: -16px;
      .item {
        padding: 40px 0 25px 0;
        display: block;
        height: auto !important;
        min-height: 20px;
        overflow: hidden;
        button {
          background: #F4F4F4;
          border: 1px solid #E5E5E5;
          border-radius: 23px;
          height: 45px;
          line-height: 21px;
          font-size: 16px;
          float: left;
          margin-top: 15px;
          color: #666;
          margin: 15px 2% 0px 1%;
        }
        button:hover {
          color: #FFFFFF;
          background: #2B2EF5;
          border: none;
        }
      }
      img {
        height: 180px;
        width: 100%;
        border-radius: 3px;
        display: flex;
        float: left;
        margin-bottom: 15px;
        border: 1px solid #f6f6f6;
      }
      span {
        display: inline-block;
        float: left;
        font-size: 16px;
        color: #000000;
        text-align: left;
        padding:0px 20px;
        width: 100%;
        .title {
          font-size: 22px;
          color: #000000;
          font-weight: bold;
          text-align: center;
          margin-bottom: 10px;
        }
        .small_bh {
          margin-left: 10px;
          font-size: 16px;
          color: #9B9B9B;
          float: right;
          text-align: center;
          display: block;
        }
        .price {
          font-size: 20px;
          color: #2B2EF5;
          font-weight: bold;
          display: inline-block;
        }
      }
    }
	}
</style>



