<template>
	<div class="layout" :data="yue">
    <div class="top_banner">
      <p >
        <span >账户提现</span></p>
    </div>
		<Content class="content6 top_p">
			<div class="hm_product_num">
				<div class="boxbg">
					<p class="price">¥{{price}}</p>
					<p class="tx_but" @click='zhtx'>去提现</p>
				</div>
			</div>
			</Content>
		<Content class="content6">
			<div class="hm_product_num">
				<Row :gutter="16" class="big_h">
					<Col span="24">
					<div class="my_list" v-for="item in jyjl_list">
						<span class="left">
									<img :src='item.src' />
									<p class="title">{{item.title}}</p></span>
									<p class="title">{{item.data}}</p></span>
					</div>
					</Col>
				</Row>
			</div>
		</Content>
	</div>
</template>
<script>
	import { applylist } from '../../api/api';
	export default {
		name: 'pri_zh',
		data() {
			return {
				yue:[],
				src: require('../../assets/deta_img.png'),
				price:this.$route.query.balance,
				title: '账户余额',
				jyjl_list:[]
			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品列表
			getProducts() {
				let para = {
					pageNum: this.pageNum,
					pageSize: this.pageSize,
					customerId: sessionStorage.customerId,
					applyType: 2,
					status:1,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					//console.log(res)
					this.yue = res.data.result.result;
					this.listLoading = false;
					//console.log(this.yue)
					//NProgress.done();
				});
			},
			zhtx() {
				this.$router.push({
					path: '/zhtx'
				})
			},
		},
		props: {},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width:1200px;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.boxbg {
			height: 130px;
      margin-top: 20px;
			display: block;
			.price {
				font-size: 40px;
				color: #000000;
				text-align: left;
				line-height: 40px;
				font-weight: bold;
			    text-align:center;
			}
		.tx_but {
			border: 2px solid #2B2EF5;
			border-radius: 100px;
			width: 220px;
			height: 56px;
      font-size: 20px;
			line-height: 55px;
			color: #2B2EF5;
			display: block;
			margin:10px auto;
		}
		}
		.my_list {
			overflow: hidden;
			height: 55px;
			line-height: 55px;
			border-bottom: 1px solid #E6E6E6;
			display: flex;
			justify-content: space-between;
			.go {
				align-content: flex-end;
				font-size: 22px;
				vertical-align: middle;
				line-height: 55px;
				color: #8C8C8C;
			}
			.left {
				align-content: flex-start;
				img {
					border-radius: 6px;
					float: left;
					margin: 17px 8px 15px 0;
					align-content: flex-start;
				}
				.title {
					border-radius: 0 0 6px 6px;
					bottom: 0px;
					font-size: 16px;
					color: #000000;
					margin: 0;
					width: 200px;
					text-align: left;
					align-content: flex-start;
				}
			}
		}
	}
</style>



