<template>
	<div class="footer">
		<Row type="flex">
			<Col span="12">
			<p @click="djcz"><img :src=sczt style="margin-right: 10px;" />{{mes}}</p>
			</Col>
			<Col span="12">
			<p class="buy" @click="ok"><img src="../../assets/goumai.png" /></p>
			</Col>
		</Row>
	</div>
</template>

<script>
	import {
		Collectcreate,
		CollectgetById,
		Collectdelete
	} from '../../api/api';
	export default {
		name: 'buyservice',
		data() {
			return {
				modal22: false,
				modal23: false,
				sczt: require('../../assets/shoucang.png'),
				mes: '收藏',
        applyType:'',
				tankzt: 'modal22= true'
			}
		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				} else {
					let para = {
						actionType: '2',
						contentType: applyType,
						id: this.$route.query.id,
						customerId: sessionStorage.customerId
					};
					CollectgetById(para).then((res) => {
						// console.log(res.data.code)
						if(res.data.code == '0') {
							this.sczt = require('../../assets/ysc@2x.png');
							this.mes = '收藏成功'
						}
					})
				}
			},
			ok() {
				this.$Message.success('开始订单支付');
				this.$router.push({
					path: '/zhifuding',
					query: {
						id: this.$route.query.id,
						applyType: this.$route.query.applyType,
						name: this.$route.query.name,
						price: this.$route.query.price,
					}
				})
			},
			cancel() {
				this.$Message.warning('取消操作');
			},
			djcz(){
				if(this.mes=="收藏"){
				this.listLoading = true;
				//NProgress.start();
				let applyType = this.$route.query.applyType;
				let para = {
					actionType: '2',
					contentType: applyType,
					targetId: this.$route.query.id,
					customerId: sessionStorage.customerId
				};
				Collectcreate(para).then((res) => {
					console.log(res);
					this.listLoading = false;
					this.sczt = require('../../assets/ysc@2x.png');
					if(res.data.code == '0') {
						this.mes = '收藏'
						this.$Message.success({
							content: '收藏成功',
							duration: 3
						});
						this.onload();
					} else {
						this.$Message.warning({
							content: '收藏失败',
							duration: 3
						});
					}
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
				} else if(this.mes=="收藏成功"){
				this.listLoading = true;
				//NProgress.start();
				let applyType = this.$route.query.applyType;
				let para = {
					actionType: '2',
					targetId: this.$route.query.id,
					customerId: sessionStorage.customerId
				};
				Collectdelete(para).then((res) => {
					this.products = res.data.result;
					this.listLoading = false;
					if(res.data.code == '0') {
						this.sczt = require('../../assets/shoucang.png');
						this.mes = '收藏成功'
						this.$Message.success({
							content: '取消收藏成功',
							duration: 3
						});
						this.onload();
					} else {
						this.$Message.warning({
							content: '取消收藏失败',
							duration: 3
						});
					}
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				})
				}
			},
		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.onload();
		},
		watch: {
			'sczt': function(newVal) {
				this.sczt = newVal
			},
			'mes': function(newVal) {
				this.mes = newVal
			},
		}
	}
</script>

<style lang="less" scoped>
	.tkwz {
		font-size: 18px;
		color: #000000;
	}

	.footer {
		background-color: #FFFFFF;
		width: 100%;
		p {
			line-height: 40px;
			text-align: right;
			color: #000;
			font-size: 18px;
		}
		img {
			height: 23px;
			display: inline-block;
			margin: auto;
			vertical-align: text-top;
		}
		.line {
			margin-left: -1px;
			margin-top: 15px;
			height: 30px;
			width: 1px;
			display: inline-block;
			vertical-align: middle;
			background-color: #D7D7D7;
		}
		.buy {
      background: -webkit-linear-gradient(left top, #7EE0FF, #35AAFF);
      background: -o-linear-gradient(bottom right, #7EE0FF, #35AAFF);
      background: -moz-linear-gradient(bottom right, #7EE0FF, #35AAFF);
      background: linear-gradient(to bottom right, #7EE0FF, #35AAFF);
			display: block;
      float: right;
			height: 40px;
      padding: 10px;
      border-radius: 40px;
			color: #FFFFFF;
			font-size: 18px;
			img {
				display: inline-block;
        width: 100%;
        height: 100%;
				vertical-align: top;
			}
		}
	}
</style>



