<template>
	<div class="layout">
    <topMeau></topMeau>
    <div class="top_banner">
      <p >
        <span>升级VIP</span></p>
    </div>
		<Content class="content2">
      <div class="hm_product_num">
			<div class="vip_box">
				¥{{price}}/年
			</div>
				<Row :gutter="16">
					<Col span="24">
					<span class="big_title"><img src="../../assets/add.png" /> 会员权益</span>
					</Col>
					<Col span="24">
					<div class="my_list" v-for="item in yhj_list">
						{{item.title}}
					</div>
					</Col>
				</Row>

				<Button type="primary" shape="circle" @click="ok(price)">立即升级</Button>
			</div>
		</Content>
	</div>
</template>
<script>
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'sjvip',
		data() {
			return {
				price: 2000,
				title: '升级VIP',
				yhj_list: [{
						title: '平台所有交易服务费9折优惠券',
				},
				]
			}
		},
		components: {
      topMeau
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			ok(price) {
				this.$router.push({
					path: '/zhifuding',
					query:{
						id: sessionStorage.customerId,
						price:price,
					    applyType:'4',
					    name:'升级会员'
					}

				})
			},
			cancel() {
				this.$Message.warning('提交失败');
			}
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.vip_box {
		height: 162px;
		line-height: 162px;
		width: 100%;
		display: block;
		margin: auto;
		background-color: #E2B500;
		border-radius: 6px;
		color: #FFFFFF;
		font-size:32px;
		font-weight: bold;
	}

	.hm_product_num {
    width: 50%;
    margin: 20px auto;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.big_title {
			font-size: 18px;
			color: #333333;
			line-height: 50px;
			vertical-align: middle;
		}
		.my_list {
			border-radius: 0 0 6px 6px;
			font-size: 16px;
			display: inline-block;
			color: #000000;
			margin: 0 auto;
			width: 50%;
			background: url(../../assets/vip_bg.png) 100% no-repeat;
			background-position: 0px 0px;
			height: 45px;
			text-align: center;
			line-height: 45px;
			text-indent: 16px;
			margin-bottom: 15px;
		}
	}
</style>



