<template>
	<div class="layout">
    <topMeau></topMeau>
    <div class="top_banner">
      <p >
        <span>修改密码</span></p>
    </div>
		<div class="content6 ">
      <div class="hm_product_num">
			<Form ref="formCustom" :model="formCustom" :rules="ruleCustom">
				<FormItem prop="passwd">
					<Input class="suk" type="password" v-model="formCustom.passwd" placeholder="请设置密码">
					<Icon type="ios-lock-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem prop="passwdCheck">
					<Input class="suk" type="password" v-model="formCustom.passwdCheck" placeholder="请确认密码">
					<Icon type="ios-lock-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem>
					<Button type="primary" shape="circle" @click="handleSubmit('formCustom')">提交</Button>
				</FormItem>
			</Form>
		</div>
    </div>
	</div>
</template>

<script>
	import { getUserchangePwd } from '../../api/api';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'wjmm',
		data() {
			const validatePass = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请设置密码'));
				} else {
					if(this.formCustom.passwdCheck !== '') {
						// 对第二个密码框单独验证
						this.$refs.formCustom.validateField('passwdCheck');
					}
					callback();
				}
			};
			const validatePassCheck = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('请确认密码'));
				} else if(value !== this.formCustom.passwd) {
					callback(new Error('两次输入的密码不一致'));
				} else {
					callback();
				}
			};
			return {
				formCustom: {
					passwd: '',
					passwdCheck: '',
				},
				ruleCustom: {
					passwd: [{
						validator: validatePass,
						trigger: 'blur'
					}],
					passwdCheck: [{
						validator: validatePassCheck,
						trigger: 'blur'
					}],
				}
			}
		},
		components: {topMeau},
		props: {},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					let _this = this;
					if(valid) {
						let para = {
						   loginName:sessionStorage.loginName,
                           customerId:sessionStorage.customerId,
                           newPwd: this.formCustom.passwd,
                           type:1,
						};
						getUserchangePwd(para).then((res) => {
							if(res.data.code == 0) {
								this.$Message.success("修改成功");
								this.$router.push({
									path: '/gmcg'
								})
							} else {
								this.$Message.warning("修改失败");
								this.$router.push({
									path: '/gmsb'
								})
							}
						});
					} else {
						this.$Message.error('输入有误!');
					}
				})
			}
		},
	}
</script>

<style lang="less" scoped>
	.login_logo {
		height: 180px;
		width: 100%;
		line-height: 240px;
		display: block;
		text-align: center;
		background-color: #2B2EF5;
		img {
			width: 129px;
			height: 71px;
			display: inline-block;
		}
	}
.hm_product_num{
  width: 50%;
  margin: 20px auto;
}
	.content6 {
		padding: 10px 20px;
		background-color: #FFFFFF;
		.suk {
			margin: 15px 0 -10px 0;
			display: block;
			background-color: #F6F6F6;
			border-radius: 50px;
		}
	}
</style>



