<template>
	<div class="layout">
    <Content style="width: 100%; background-color:#06253F;">
      <div  style="width: 1200px; height: 40px; line-height: 40px; font-weight: bold; margin: 0 auto">
        <span style="float: left; font-size: 14px; color: #ffffff;"><a @click="home()">商标转让、许可的自由买卖平台</a></span>
        <span style="float: right; font-size: 14px; color: #ffffff;">
      <a @click="zhuc()" style="margin-left: 10px">注册</a>
      </span>
      </div>
    </Content>
		<div class="login_logo  top_p">
      <a @click="home()"><img src="../../assets/login_logo.png" /></a>
		</div>
		<div class="content6">
			<Form ref="formCustom" :data="formCustom" :model="formCustom" :rules="ruleCustom" style="width: 600px; padding: 0 100px; margin: 0 auto;">
				<FormItem prop="loginName">
					<Input class="suk" v-model="formCustom.loginName" placeholder="请输入手机号码/邮箱/用户名">
					<Icon type="ios-contact-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem prop="passwd">
					<Input class="suk" type="password" v-model="formCustom.passwd" placeholder="请输入密码">
					<Icon type="ios-lock-outline" slot="prefix" />
					</Input>
				</FormItem>
				<FormItem>
					<Button type="primary" shape="circle" @click="handleSubmit('formCustom')">登录</Button>
          <!--<Button  shape="circle" style="width: 100%; height: 50px; font-size: 18px;" @click="zhuc">注册</Button>-->
					<p class="but_right" @click="wjmm">忘记密码?</p>
				</FormItem>
			</Form>
		</div>
	</div>
</template>

<script>
  import topHeader from './../public/top.vue';
	import { getUserlogin, getcustomergetById } from '../../api/api';
	export default {
		name: 'home',
		data() {
			return {
				formCustom: {
					passwd: '',
					loginName: ''
				},
				ruleCustom: {
					passwd: [{
						required: true,
						message: '密码不能为空',
						trigger: 'blur'
					}],
					loginName: [{
						required: true,
						message: '请输入手机号码/邮箱/用户名',
						trigger: 'blur'
					}]
				}
			}
		},
		components: {topHeader},
		methods: {
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if(valid) {
						let para = {
							loginName: this.formCustom.loginName,
							password: this.formCustom.passwd,
						};
						getUserlogin(para).then((res) => {
							if(res.data.code == 0) {
								this.$Message.success('登录成功!');
								sessionStorage.loginName = this.formCustom.loginName;
								sessionStorage.password = this.formCustom.passwd;
								sessionStorage.customerId = res.data.result.customerId;
								this.$router.push({
									path: '/my',
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
						this.$Message.error('输入有误');
					}
				})
			},
			wjmm() {
				this.$router.push({
					path: '/wjmm'
				})
			},
			zhuc() {
				this.$router.push({
					path: '/zhuc'
				})
			},
      home(){
        this.$router.push({
          path: '/',
        })
      },
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
		},
		props: {},
		computed: {},
	}
</script>

<style lang="less" scoped>
  a {
    color: #ffffff;
  }
	.login_logo {
		height: 190px;
		width: 100%;
		line-height: 200px;
		display: block;
		text-align: center;
		background-color: #06253F;
		img {
			width: 112px;
			height: 96px;
			display: inline-block;
		}
	}

	.content6 {
		padding: 10px 20px;
		background-color: #FFFFFF;
		.suk {
			margin: 15px 0 -10px 0;
			display: block;
			background-color: #F6F6F6;
			border-radius: 50px;
		}
		.but_left {
			float: left;
			font-size: 14px;
		}
		.but_right {
      margin-top: 30px;
			font-size: 14px;
		}
	}
</style>



