<template>
	<div class="layout">
    <topMeau></topMeau>
		<Content class=" top_p">
			<div class="hm_product_num">
				<div class="per_top">
					<div class="per_avatar" :model="person">
						<img :src="person.headLogo" />
					</div>
					<div class="per_box" v-model="person">
							<span class="per_mess" v-if="person.vipFlag==='N'"><p @click="sjvip"><img src="../../assets/V.png"/>升级VIP></p>
                </span>
						<span class="per_mess" v-else><p @click="sjvip" class="vip"><img src="../../assets/vip.png"/>VIP会员</p></span>
            <span class="per_mess"><p class="per_title">{{person.loginName}}</p></span>
            <span>
              <div class="price"><p  @click="pri_zh(person.balance)">¥{{person.balance}}</p></div>
						</span>
				</div>
				</div>
		<div class=" bottom_p">
      <Row  :gutter="24" type="flex" style="padding-left: 20px">
        <Col span="7" class="box_list left_box">
          <span class="title_box">我是买家</span>
          <Row :gutter="16" class="big_h">
            <Col span="12">
              <a @click='sbgl' class="my_list">
								<span class="left">
									<img src="../../assets/sbicon.png" />
									<p class="title">商标管理 </p></span>
              </a>
            </Col>
            <Col span="12">
              <a @click='zlgl' class="my_list">
								<span class="left">
									<img src="../../assets/zlicon.png" />
									<p class="title">专利管理</p></span>
              </a>
            </Col>
          </Row>
        </Col>
        <Col span="7" class="box_list center_box">
          <span class="title_box">我是卖家</span>
          <Row :gutter="16" class="big_h">
            <Col span="12">
              <a @click='sb_order' class="my_list">
								<span class="left">
									<img src="../../assets/jiaoyi.png" />
									<p class="title">我的订单</p></span>
              </a>
            </Col>
            <Col span="12">
              <a @click='myqg' class="my_list">
								<span class="left">
									<img src="../../assets/order1.png" />
									<p class="title">我的求购</p></span>
              </a>
            </Col>
          </Row>
        </Col>
        <Col span="8" class="box_list right_box">
          <span class="title_box">更多设置</span>
          <Row :gutter="16" class="big_h">
            <Col span="8">
              <a @click='myfx' class="my_list">
						<span class="left">
									<img src="../../assets/scicon.png" />
									<p class="title">我的收藏</p></span>
              </a>
            </Col>
            <Col span="8">
              <a @click='xgmm' class="my_list">
						<span class="left">
									<img src="../../assets/order4.png" />
									<p class="title">修改密码</p></span>
              </a>
            </Col>
            <Col span="8">
              <a @click='yjfk' class="my_list">
						<span class="left">
									<img src="../../assets/order5.png" />
									<p class="title">意见反馈</p></span>
              </a>
            </Col>
          </Row>
        </Col>
      </Row>
		</div>
      </div>
    </Content>
	</div>
</template>
<script>
	import { getcustomerlist, getcustomergetById,configgetList } from '../../api/api';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'my',
		data() {
			return {
				person: {
					headLogo: require('../../assets/atavar.png'),
					loginName: '编辑个人资料',
					balance:'***',
					vipFlag:'N'
				},
			}
		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
				sessionStorage.clear();
					this.$router.push({
						path: '/login'
					})
				}else{
				this.listLoading = true;
				//NProgress.start();
				let customerId =sessionStorage.customerId;
				getcustomergetById({customerId}).then((res) => {
					this.person = res.data.result;
					sessionStorage.vipFlag = this.person.vipFlag;
					sessionStorage.mobile = this.person.mobile;
					if( res.data.code == "20004" ) {
				    sessionStorage.clear();
						this.$router.push({
							path: '/login'
						})
					} else if(this.person.loginName === null, this.person.headLogo === null) {
						this.person.loginName = '编辑个人资料';
						this.person.headLogo = require('../../assets/atavar.png');
					} else {
						this.person.loginName = this.person.loginName;
					}
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			  }
			},
			configList() {
				let para = {};
				configgetList(para).then((res) => {
					this.data = res.data.result;
					sessionStorage.vipzk = this.data[4].value;
					sessionStorage.brand_fee = this.data[5].value;
					sessionStorage.patent_fee = this.data[6].value;
					sessionStorage.telephone = this.data[7].value;
				}).catch((err) => {
					//console.log(err);
				});
			},
			sjvip() {
				this.$router.push({
					path: '/sjvip'
				})
			},
			glzh() {
				this.$router.push({
					path: '/glzh'
				})
			},
			pri_zh(balance) {
				this.$router.push({
					path: '/pri_zh',
					query:{
						balance:balance
					}
				})
			},
			sbgl() {
				this.$router.push({
					path: '/sbgl',
				})
			},
			zlgl() {
				this.$router.push({
					path: '/zlgl'
				})
			},
			myjy() {
				this.$router.push({
					path: '/myjy'
				})
			},
			myzj() {
				this.$router.push({
					path: '/myzj'
				})
			},
			myfx() {
				this.$router.push({
					path: '/myfx'
				})
			},
			xgmm() {
				this.$router.push({
					path: '/xgmm'
				})
			},
			yjfk() {
				this.$router.push({
					path: '/yjfk'
				})
			},
			sb_order() {
				this.$router.push({
					path: '/sb_order'
				})
			},
			zl_order() {
				this.$router.push({
					path: '/zl_order'
				})
			},
			myqg() {
				this.$router.push({
					path: '/myqg'
				})
			},
			myyhq() {
				this.$router.push({
					path: '/myyhq'
				})
			},
      home(){
        this.$router.push({
          path: '/'
        })
      },
			exit() {
				sessionStorage.clear();
				this.$Message.success('退出成功');
				this.$router.push({
					path: '/'
				})
				//console.log(sessionStorage)
			},
			cancel() {
				this.$Message.warning('退出失败');
			},
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
		},
		props: {},
    components: {topMeau},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.configList();
			this.onload();
		}
	}
</script>

<style lang="less" scoped>
  a {
    color: #ffffff;
  }
	.bg_box {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 152px;
		display: block;
		background-image: url('../../assets/ser_bg.png');
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 152px;
		font-weight: bolder;
	}

	.top {
		margin-top: 152px;
	}

	.hm_product_num {
		width: 1200px;
		display: block;
		height: auto !important;
		min-height: 20px;
    margin: 0 auto;
		overflow: hidden;
		.per_top {
			height: 250px;
      padding: 35px 0;
      text-align: center;
			.per_avatar {
				display: inline-block;
				width: 100px;
				height: 100px;
				border-radius: 53px;
				margin-right: 10px;
				background-color: #E6E6E6;
				img {
          width: 100px;
          height: 100px;
					border-radius: 130px;
					border: none;
				}
			}
			.per_box {
        display: block;
        padding: 10px 0;
				.icon_right {
					float: right;
				}
				.per_mess {
					display: block;
					.vip {
						color: #FF5D00;
					}
					.per_title {
						font-size: 22px;
						color: #000000;
						text-align: left;
						font-weight: bold;
						display: inline;
            margin-bottom: 10px;
					}
					p {
						img {
							margin-right: 5px;
						}
						display: inline-block;
						font-size: 11px;
						color: #9B9B9B;
						letter-spacing: 0.26px;
					}
				}
				.price {
					display: block;
					height: 26px;
					font-weight: bold;
          text-align: center;
					font-size: 28px;
					color: #2B2EF5;
					line-height: 26px;
				}
			}
		}
	}
  .left_box {
    background: -webkit-linear-gradient(left top, #7EE0FF, #35AAFF);
    background: -o-linear-gradient(bottom right, #7EE0FF, #35AAFF);
    background: -moz-linear-gradient(bottom right, #7EE0FF, #35AAFF);
    background: linear-gradient(to bottom right, #7EE0FF, #35AAFF);
  }
  .center_box{
    background: -webkit-linear-gradient(left top, #E95EFF, #512CFF);
    background: -o-linear-gradient(bottom right, #E95EFF, #512CFF);
    background: -moz-linear-gradient(bottom right, #E95EFF, #512CFF);
    background: linear-gradient(to bottom right, #E95EFF, #512CFF);
  }
  .right_box{
    background: -webkit-linear-gradient(left top, #FFC586, #FF882C);
    background: -o-linear-gradient(bottom right, #FFC586, #FF882C);
    background: -moz-linear-gradient(bottom right, #FFC586, #FF882C);
    background: linear-gradient(to bottom right, #FFC586, #FF882C);
  }
  .box_list{
    margin: 20px 10px;
    border-radius: 6px;
    display: block;
    height: 260px;
    padding: 0 30px;
    .title_box{
      font-size: 22px;
      height: 110px;
      line-height: 100px;
      color: #ffffff;
      font-weight: bold;
      display: block;
      text-align: left;
      text-indent: 20px;
      opacity: 0.6;
    }
    .big_h {
      margin-top: -16px;
    }
    .my_list {
      overflow: hidden;
      text-align: center;
      .left {
        img {
          border-radius: 52px;
          width: 80px;
          height: 80px;
          margin-bottom: 10px;
        }
        .title {
          font-weight: bold;
          font-size: 16px;
          color: #ffffff;
        }
      }
    }
  }
  .line {
    width: 100%;
    background-color: #E6E6E6;
    margin-bottom: 20px;
    height: 1px;
    padding: 0;
  }
</style>



