<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <div class="top_banner">
      <p >
        <span >{{categoryFir}}·专利</span></p>
    </div>
		<Content class="content6 top_p">
			<div class="hm_product_num">
				<Row type="flex" :gutter="24">
					<Col span="8" v-for="(item, name, index) in cate_list" :key="index">
					<p @click="zl_list(item.categorySec)" class="zl_meau_box">
						<span  class="sty1_box"  :class=item.class><img :src=item.src /> </span>{{item.categorySec}}
					</p>
					</Col>
				</Row>

			</div>
		</Content>
	</div>
</template>

<script>
	import footmeau from './../public/footmeau.vue';
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'zl_meau',
		data() {
			return {
				categoryFir: this.$route.query.categoryFir,
				cate_list: [{
					categorySec: '发明型',
					src: require('../../assets/fmx.png'),
          class:'sty1'
				}, {
					categorySec: '实用型',
					src: require('../../assets/syx.png'),
          class:'sty2'
				}, {
					categorySec: '外观型',
					src: require('../../assets/wgx.png'),
          class:'sty3'
				}]
			}
		},
		components: {
			footmeau,
      topHeader,
      topMeau
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			zl_list(categorySec) {
				this.$router.push({
					path: '/zl_list',
					query: {
						type: this.$route.query.categoryFir,
						categoryFir: this.$route.query.value,
						categorySec: categorySec,
                        applyType: this.$route.query.applyType,
					}
				})
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
    .sty1{
      background-color: #52b998;
    }
    .sty2{
      background-color: #1969f6;
    }
    .sty3{
      background-color: #5f42e2;
    }
    .sty4{
      background-color: #ee5b36;
    }
		.zl_meau_box {
			width: 100%;
			display: block;
			font-size: 16px;
			color: #000000;
			height: 180px;
			margin: 35px auto 20px auto;
			text-align: center;
      .sty1_box{
        height: 130px;
        line-height: 130px;
        margin: 0 20% 15px 20%;
        width: 60%;
        align-items: center;
        display: flex;
        border-radius: 8px;
      }
			img {
				height: 93px;
				width: 93px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
	}
</style>



