<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
		<div class="top_banner">
			<p >
			<span>服务专业 省心省力</span>
			最快响应速度 最便捷服务通道</p>
		</div>
		<Content class="content2">
			<div class="hm_product_num bottom_p">
				<Row type="flex" class="four_sever" :gutter="24">
					<Col span="4"  v-for="(item, index) in service_list"  :key="index">
					<p @click='ser_deta(item.id,item.name)'><span class="icon_box"  :class="item.class"><img :src="item.src" /></span>{{item.name}}</p>
					</Col>
				</Row>
				<Row class="sev_list" type="flex" :gutter="24">
					<Col span="4" v-for="(item, index) in service_list2" :key="index">
					<span  @click='ser_deta(item.id)'  :class="item.class"><img :src="item.src" />{{item.name}}</span>
					</Col>
				</Row>
				<Row type="flex" class="four_sever" :gutter="24">
					<Col span="4"  v-for="(item, index) in service_list3"  :key="index">
            <p @click='ser_deta(item.id)'> <span class="icon_box"  :class="item.class"><img :src="item.src" /></span>{{item.name}}</p>
					</Col>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'home',
		data() {
			return {
				service_list:[
				{
					id: '35',
					name: '注册商标',
					src: require('../../assets/sever_1.png'),
          class:'sty1'
				},
				{
					id: '37',
					name: '商标续费',
					src: require('../../assets/sever_2.png'),
          class:'sty2'
				},
				{
					id: '36',
					name: '商标变更',
					src: require('../../assets/sever_3.png'),
          class:'sty3'
				},
				{
					id: '38',
					name: '商标转让',
					src: require('../../assets/sever_4.png'),
          class:'sty4'
				},
				{
					id: '39',
					name: '商标撤回',
					src: require('../../assets/sever_5.png'),
          class:'sty2'
				},
				{
					id: '40',
					name: '专利服务',
					src: require('../../assets/sever_6.png'),
          class:'sty3'
				}],

				service_list2:[
				{
					id: '43',
					name: '商标变更异议申请',
          src: require('../../assets/sever-icon.png'),
          class:'sty31'
				},
				{
					id: '44',
					name: '撤销三年不使用',
          src: require('../../assets/sever-icon.png'),
          class:'sty31'
				},
				{
					id: '45',
					name: '不注册复审申请',
          src: require('../../assets/sever-icon.png'),
          class:'sty31'
				},
				{
					id: '46',
					name: '商标无效宣告申请',
          src: require('../../assets/sever-icon.png'),
          class:'sty31'
				},
				{
					id: '47',
					name: '撤销三年答辩',
          src: require('../../assets/sever-icon.png'),
          class:'sty31'
				},
				{
					id: '48',
					name: '商标无效宣告答辩',
          src: require('../../assets/sever-icon.png'),
          class:'sty31'
				}
				],
				service_list3:[
          {
            id: '41',
            name: '补注册证',
            src: require('../../assets/sever_7.png'),
            class:'sty4'
          },
          {
            id: '42',
            name: '不予册复',
            src: require('../../assets/sever_8.png'),
            class:'sty1'
          },
				{
					id: '49',
					name: '商标注销',
					src: require('../../assets/sever_9.png'),
          class:'sty1'
				},
				{
					id: '50',
					name: '异议答辩',
					src: require('../../assets/sever_10.png'),
          class:'sty2'
				},
				{
					id: '51',
					name: '驳回复审',
					src: require('../../assets/sever_11.png'),
          class:'sty3'
				},
				{
					id: '52',
					name: '其他服务',
					src: require('../../assets/sever_12.png'),
          class:'sty4'
				},
				]
			}
		},
		components: {
      topHeader,
      topHeader,
      topMeau
		},
		methods: {
			ser_deta(id,name) {
				this.$router.push({
					path: '/service_details',
					query:{
						id:id,
						name:name,
						applyType:'0'
					}
				})
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.ser_banner {
		background-image: url(../../assets/ser_banner.png);
		height: 190px;
		width: 100%;
		display: block;
		.ser_header {
			font-size: 18px;
			width: 100%;
			color: #fff;
			font-weight: bold;
			padding: 0 20px;
			height: 60px;
			line-height: 60px;
			display: block;
			text-align: center;
		}
		.big_title {
			font-family: PingFang-SC-Bold;
			font-size: 26px;
			color: #FFFFFF;
		}
		.banner_p {
			font-family: PingFang-SC-Regular;
			font-size: 13px;
			color: #FFFFFF;
		}
	}

	.hm_product_num {
		width: 1200px;
    margin: 0 auto;
		padding:0 0 20px 0;
		display: block;
		height: auto !important;
		min-height: 510px;
		background-color: #FFFFFF;
		overflow: hidden;
    .sty1{
      background-color: #52b998;
    }
    .sty2{
      background-color: #1969f6;
    }
    .sty3{
      background-color: #5f42e2;
    }
    .sty4{
      background-color: #ee5b36;
    }
    .sty31{
      background-color: #062640;
    }

		.four_sever {
			margin: 0;
			display: flex;
			font-size: 14px;
			color: #000000;
			text-align: center;
			p {
				justify-content: center;
        text-align: center;
				margin-top: 15px;
        .icon_box{
          line-height: 120px;
          align-items: center;
          justify-content: center;
          display: flex;
          border-radius: 8px;
          height: 120px;
          margin-bottom: 10px;
        }
			}
		}
		.sev_list {
			margin: 30px 0;
			span {
				display: block;
				font-size: 16px;
        font-weight: bold;
        padding: 25px 0;
        text-align: center;
        border-radius: 8px;
        color:#6DFBFB;
        height: 120px;
        background: -webkit-linear-gradient(left top, #0237C8, #05243E);
        background: -o-linear-gradient(bottom right, #0237C8, #05243E);
        background: -moz-linear-gradient(bottom right, #0237C8, #05243E);
        background: linear-gradient(to bottom right, #0237C8, #05243E);
        img{
          height:23px;
          width: 17px;
          display: block;
          margin:0 auto 15px auto;
        }
			}
		}
	}
</style>



