<template>
	<div class="layout">
    <topMeau></topMeau>
    <div class="top_banner">
      <p >
        <span style="cursor: pointer"   @click='fbqg'>+发布求购</span></p>
    </div>
		<Content class="content2 ">
			<div class="hm_product_num">
				<Tabs value="name1" style="width: 100%;">
					<TabPane label="商标求购类" name="name1" span="8">
            <Row type="flex" :gutter="24" class="box_ul">
              <Col span="8"   class="item" v-for="(item, name, index) in sb_list"  :key="index">
								<span @click='sb_qg(item.id,item.actionType,item.contentType)'><p class="title">{{item.title}}</p> <p class="price">¥{{item.budget}}</p> <p class="text-con">简介：{{item.jsonStr}}</p></span>
								<span>
								<Button type="primary" shape="circle" @click="ok(item.id)">删除</Button>
								</span>
							</Col>
						</Row>
			</TabPane>

			<TabPane label="专利求购类" name="name2" span="8" style="width: 100%;">
        <Row type="flex" :gutter="24" class="box_ul">
          <Col span="8"   class="item" v-for="(item, name, index) in zl_list"  :key="index">
						<span  @click='zl_qg(item.id,item.actionType,item.contentType)'><p class="title">{{item.title}}</p> <p class="price">¥{{item.budget}}</p> <p class="text-con">简介：{{item.jsonStr}}</p></span>
						<span>
						<Button type="primary" shape="circle" @click="ok2(item.id)">删除</Button>
						</span>
					</Col>
				</Row>
	</TabPane>
	</Tabs>
	</div>
	</Content>
	</div>
</template>

<script>
	import {
		Collectlist,Collectdelete
	} from '../../api/api';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'myqg',
		data() {
			return {
				sb_list: [],
				zl_list: [],
			}
		},
		components: {topMeau},
		props: {},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取商标列表
			getsbProducts() {
				let para = {
					customerId: sessionStorage.customerId,
					actionType:'1',
					contentType:'1'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {
					this.sb_list = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			//获取专利列表
			getzlProducts() {
				let para = {
					customerId: sessionStorage.customerId,
					actionType:'1',
					contentType:'2'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {
					this.zl_list = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			fbqg() {
				this.$router.push({
					path: '/fbqg'
				})
			},
			ok(id) {
				let para = {
                    id:id,
					customerId: sessionStorage.customerId,
					actionType:'1',
					contentType:'1'
				};
				Collectdelete(para).then((res) => {
					this.listLoading = false;
					this.getProducts();
					this.$Message.success('商标删除成功');
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
					this.$Message.success('商标删除失败');
				});
			},

			ok2(id) {
				let para = {
                    id:id,
					customerId: sessionStorage.customerId,
					actionType:'1',
					contentType:'2'
				};
				Collectdelete(para).then((res) => {
					this.listLoading = false;
					this.getProducts();
					this.$Message.success('商标删除成功');
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
					this.$Message.success('商标删除失败');
				});
			},
			sb_qg(id,actionType,contentType) {
				this.$router.push({
					path: '/sb_qg',
					query:{
						id:id,
						actionType:actionType,
						contentType:contentType,
					}
				})
			},
			zl_qg(id,actionType,contentType) {
				this.$router.push({
					path: '/zl_qg',
					query:{
						id:id,
						actionType:actionType,
						contentType:contentType,
					}
				})
			}

		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getsbProducts();
			this.getzlProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		display: flex;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
    .box_ul {
      width: 100%;
      margin-top: -16px;
      .item {
        padding: 25px 0;
        display: block;
        height: auto !important;
        min-height: 20px;
        overflow: hidden;
        &:last-child{
          border: none;
        }
        button {
          background: #F4F4F4;
          border: 1px solid #E5E5E5;
          border-radius: 23px;
          height: 45px;
          line-height: 25px;
          font-size: 16px;
          float: left;
          margin-top: 15px;
          color: #666;
          margin: 15px 2% 0px 1%;
        }
        button:hover {
          color: #FFFFFF;
          background: #2B2EF5;
          border: none;
        }
      }
      img {
        height: 180px;
        width: 100%;
        border-radius: 3px;
        display: flex;
        float: left;
        margin-bottom: 15px;
        border: 1px solid #f6f6f6;
      }
      span {
        display: inline-block;
        float: left;
        font-size: 16px;
        color: #000000;
        text-align: left;
        padding:0px 20px;
        width: 100%;
        .title {
          font-size: 16px;
          color: #000000;
          font-weight: bold;
          display: block;
          text-align: center;
        }
        .text-con{
          display: block;
          text-align: center;
        }
        .small_bh {
          font-size: 20px;
          color: #9B9B9B;
          display: block;
          text-align: center;
        }
        .price {
          font-size: 20px;
          color: #2B2EF5;
          font-weight: bold;
          display: block;
          text-align: center;
        }
      }
    }
	}
</style>



