<template>
	<div class="layout">
    <topMeau></topMeau>
    <div class="top_banner">
      <p >
        <span >申请成功</span></p>
    </div>
		<Content class=" top_p">
			<div class="hm_product_num">
				<Row type="flex" :gutter="24" class="de_cont">
					<Col span="24">
					<p class="erwem"><img src="../../assets/succe.png" /></p>
					</Col>
					<Col span="24">
					<p class="small_title">提现申请已成功<br/> 预计七个工作日内到账
						<br/> 请耐心等待～
						<br/><br/></p>
					</Col>
					<Col span="24">
					<Button type="primary" shape="circle" to="/my">我知道了</Button>
					</Col>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'txcg',
		data() {
			return {}
		},
		components: {topMeau},
		methods: {
			prev() {
				this.$router.go(-1)
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.tkwz {
		font-size: 18px;
		color: #000000;
	}

	.hm_product_num {
    width: 50%;
    margin: 20px auto;
		display: block;
		height: auto !important;
		min-height: 20px;
		padding: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.erwem {
			width: 100%;
			display: block;
			text-align: center;
			margin: 30px auto 20px auto;
		}
		.small_title {
			display: block;
			width: 100%;
			font-size: 16px;
			font-weight: bold;
			color: #333333;
		}
	}
</style>



