<template>
	<div class="layout">
    <topMeau></topMeau>
    <div class="top_banner">
      <p >
        <span style="cursor: pointer"  @click='zlfb'>+发布专利</span></p>
    </div>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="出售中" name="name1">
            <Row type="flex" :gutter="24" class="box_ul">
              <Col span="8"   class="item" v-for="(item, name, index) in product_list"  :key="index">
								<span>
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
								</span>
								<span><Button @click='zlbj(item.id)' type="primary" shape="circle"> 编辑</Button>
								<Button type="primary" shape="circle" @click="ok(item.id)">删除</Button>
								</span>
							</Col>
						</Row>
					</TabPane>
					<TabPane label="交易中" name="name2" span="8">
            <Row type="flex" :gutter="24" class="box_ul">
              <Col span="8"   class="item" v-for="(item, name, index) in product_list1"  :key="index">
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
							</Col>
						</Row>
					</TabPane>
					<TabPane label="已出售" name="name3" span="8">
            <Row type="flex" :gutter="24" class="box_ul">
              <Col span="8"   class="item" v-for="(item, name, index) in product_list2"  :key="index">
								<span>
								<img :src="item.bannerUrl" />
								<span><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
								</span>
								<span style="width:100%; float: right;">
								<Button type="primary" shape="circle" @click="modal2 = true">删除</Button>
								</span>
							</Col>
						</Row>
					</TabPane>

				</Tabs>

			</div>
		</Content>
	</div>
</template>
<script>
	import { patentgetList, patentdelete } from '../../api/api';
  import topMeau from './../public/top_meau.vue';
  import topHeader from './../public/top.vue';
	export default {
		name: 'zlgl',
		data() {
			return {
				modal2: false,
				product_list: [],
				product_list1: [],
				product_list2: []

			}
		},
		components: {
      topMeau,topHeader
		},
		methods: {
			onload() {
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			//获取专利列表
			getProducts() {
				let para = {
					customerId: sessionStorage.customerId,
					pageSize:20,
					status:0,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			getProducts1() {
				let para = {
					customerId: sessionStorage.customerId,
					pageSize:20,
					status:1,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list1 = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			getProducts2() {
				let para = {
					customerId: sessionStorage.customerId,
					pageSize:20,
					status:2,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.total = res.data.result.total;
					this.product_list2 = res.data.result.result;
					//console.log(this.product_list);
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			prev() {
				this.$router.push({
					path: '/my',
				})
			},
			zlfb() {
				this.$router.push({
					path: '/zlfb',
				})
			},
			zlbj(id) {
				this.$router.push({
					path: '/zlbj',
                    query:{
						id:id
                    }
				})
			},
			ok(id) {
				let para = {
					customerId: sessionStorage.customerId,
					ids:id,
				};
				patentdelete(para).then((res) => {
					this.listLoading = false;
					this.$Message.success('专利删除成功');
					this.getProducts();
				}).catch((err) => {
					console.log(err);
					this.$Message.success('专利删除失败');
				});
			},
			cancel() {
				this.$Message.warning('已取消删除');
			},
			fenx() {
				this.$Message.success({
					content: '分享成功',
					duration: 3
				});
			},
		},
		props: {},
		mounted() {
			this.getProducts();
			this.getProducts1();
			this.getProducts2();
			this.onload();
		}
	}
</script>

<style lang="less" scoped>
	.title_right {
		display: inline-block;
		float: right;
	}

	.hm_product_num {
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
    .box_ul {
      width: 100%;
      margin-top: -16px;
      .item {
        padding: 25px 0;
        display: block;
        height: auto !important;
        min-height: 20px;
        overflow: hidden;
        &:last-child{
          border: none;
        }
        button {
          background: #F4F4F4;
          border: 1px solid #E5E5E5;
          border-radius: 23px;
          width: 47%;
          height: 33px;
          line-height: 21px;
          font-size: 16px;
          float: left;
          margin-top: 15px;
          color: #666;
          margin: 15px 2% 0px 1%;
        }
        button:hover {
          color: #FFFFFF;
          background: #2B2EF5;
          border: none;
        }
      }
      img {
        height: 180px;
        width: 100%;
        border-radius: 3px;
        display: flex;
        float: left;
        margin-bottom: 15px;
        border: 1px solid #f6f6f6;
      }
      span {
        display: inline-block;
        float: left;
        font-size: 16px;
        color: #000000;
        text-align: left;
        padding:0px 20px;
        width: 100%;
        .title {
          font-size: 20px;
          color: #000000;
          font-weight: bold;
        }
        .small_bh {
          font-size: 20px;
          color: #9B9B9B;
          float: right;
          display: inline-block;
        }
        .price {
          font-size: 18px;
          color: #2B2EF5;
          font-weight: bold;
          float: left;
          display: inline-block;
        }
      }
    }
	}
</style>



