<template>
	<div class="layout">
    <topMeau></topMeau>
    <div class="top_banner">
      <p >
        <span >我的收藏</span></p>
    </div>
		<Content class="content6 top_p2">
			<div class="hm_product_num">
				<Tabs value="name1" style="width: 100%;">
					<TabPane label="商标类" name="name1" @click="getsbProducts()">
            <Row type="flex" :gutter="24" class="box_ul">
              <Col span="8"  class="item" v-for="(item, name, index) in sb_list"  :key="index">
								<img  @click='sb_details(item.id)' :src="item.bannerUrl" />
								<span @click='sb_details(item.id)' ><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
							</Col>
						</Row>
			</TabPane>

			<TabPane label="专利类" name="name2" style="width: 100%;" @click="getzlProducts()">
        <Row type="flex" :gutter="24" class="box_ul">
          <Col span="8"   class="item"  v-for="(item, name, index) in zl_list"  :key="index">
						<img  @click='zl_details(item.id)'  :src="item.bannerUrl" />
						<span  @click='zl_details(item.id)'><p class="title">{{item.name}}</p><p class="small_bh">编号：{{item.id}}</p><p class="price">¥{{item.price}}</p></span>
					</Col>
				</Row>
	</TabPane>
	</Tabs>
	</div>
	</Content>
	</div>
</template>

<script>
	import {
		Collectlist
	} from '../../api/api';
  import topMeau from './../public/top_meau.vue';
	export default {
		name: 'myxj',
		data() {
			return {
				sb_list: [],
				zl_list: [],
			}
		},
		components: {topMeau},
		props: {},
		methods: {
			//获取商标列表
			getsbProducts() {
				let para = {
					customerId: sessionStorage.customerId,
					actionType:'2',
					contentType:'1'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {
					//console.log(res);
					this.sb_list = res.data.result.result.brands;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			//获取专利列表
			getzlProducts() {
				let para = {
					customerId: sessionStorage.customerId,
					actionType:'2',
					contentType:'2'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {
					this.zl_list = res.data.result.result.patents;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},

			prev() {
				this.$router.go(-1)
			},
			sb_details(id) {
				this.$router.push({
					path: '/sb_details',
					query:{
						id:id
					}
				})
			},
			zl_details(id) {
				this.$router.push({
					path: '/zl_details',
					query:{
						id:id
					}
				})
			}

		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getsbProducts();
			this.getzlProducts();
		}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		display: flex;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;		.box_ul {
    width: 100%;
    margin-top: -16px;
    .item {
      padding: 25px 0;
      display: block;
      height: auto !important;
      min-height: 20px;
      overflow: hidden;
      &:last-child{
        border: none;
      }
      button {
        background: #F4F4F4;
        border: 1px solid #E5E5E5;
        border-radius: 23px;
        width: 47%;
        height: 33px;
        line-height: 21px;
        font-size: 16px;
        float: left;
        margin-top: 15px;
        color: #666;
        margin: 15px 2% 0px 1%;
      }
      button:hover {
        color: #FFFFFF;
        background: #2B2EF5;
        border: none;
      }
    }
    img {
      height: 180px;
      width: 100%;
      border-radius: 3px;
      display: flex;
      float: left;
      margin-bottom: 15px;
      border: 1px solid #f6f6f6;
    }
    span {
      display: inline-block;
      float: left;
      font-size: 16px;
      color: #000000;
      text-align: left;
      padding:0px 20px;
      width: 100%;
      .title {
        font-size: 20px;
        color: #000000;
        font-weight: bold;
      }
      .small_bh {
        font-size: 20px;
        color: #9B9B9B;
        float: right;
        display: inline-block;
      }
      .price {
        font-size: 18px;
        color: #2B2EF5;
        font-weight: bold;
        float: left;
        display: inline-block;
      }
    }
  }
	}
</style>



