<template>
  <div>
  <div style="width: 100%; background-color:#06253F" v-if="customerId != null">
    <div class="n_top">
      <span style="float: left;"><a @click="home()">首页·商标转让、许可的自由买卖平台</a></span>
      <span style="float: right; ">
        <a style="margin-right: 20px"  @click="My()">个人中心</a>
         <a @click="glzh">编辑资料<Icon type="ios-create-outline"  class="icon_right"/> </a>
         <a @click="exit">退出</a>
      </span>
    </div>
  </div>
  <div style="width: 100%; background-color:#f6f6f6"  v-if="customerId == null">
    <div  style="width: 1200px; height: 40px; line-height: 40px; margin: 0 auto">
      <span style="float: left; font-size: 14px; color: #000000"><router-link to="/" style="color: #333; font-weight: bold">商标转让、许可的自由买卖平台</router-link></span>
      <span class="top_title">
        <span>欢迎光临</span>
      <a @click="login()">登录</a>
      <a @click="zhuc()">注册</a>
      </span>
    </div>
  </div>
  </div>
</template>

<script>
    export default {
      name: "topMeau",
      data (){
          return{
            customerId: sessionStorage.customerId,
            Name: sessionStorage.loginName
          }
      },
      methods:{
        home(){
          this.$router.push({
            path: '/'
          })
        },
        exit() {
          sessionStorage.clear()
          this.$Message.success('退出成功')
          this.$router.push({
            path: '/login'
          })
          //console.log(sessionStorage)
        },
        glzh() {
          this.$router.push({
            path: '/glzh'
          })
        },
        login(){
          this.$router.push({
            path: '/login',
          })
        },
        zhuc() {
          this.$router.push({
            path: '/zhuc'
          })
        },
        My(){
          this.$router.push({
            path: '/my'
          })
        }
      }
    }
</script>

<style  lang="less" scoped>
  a{
    color: #ffffff;
  }
  a:hover{
    color: #91d5ff;
  }
  .top_title {
    float: right;
    font-size: 14px;

    span {
      display: inline-block;
    }

    a {
      &:first-child {
        color: #2d8cf0;
      }
      margin-left: 15px;
      width: 120px;
      text-align: center;
      font-weight: bold;
      color: #000;
    }
  }
  .n_top{
    width: 1200px;
    height: 40px;
    font-weight: bold;
    line-height: 40px;
    margin: 0 auto;
    font-size: 14px;
    color: #ffffff;
  .icon_right {
    font-size: 18px;
    color: #ffffff;
    margin-right: 20px;
    margin-left: 3px;
    font-weight: bold;
    vertical-align: middle;
    margin-bottom: 3px;
  }
  }
</style>



