<template>
	<div class="layout">
    <topMeau></topMeau>
    <topHeader></topHeader>
    <div class="top_banner">
      <p>
        <span>{{products.name}}</span>
      </p>
    </div>
		<Content v-model="products">
			<div class="hm_product_num">
        <span class="buyseve">
        <buyservice v-if="products.status===0"></buyservice>
        <buyservice v-if="products.status===null"></buyservice>
        </span>
        <img :src=products.bannerUrl class="img" />
				<Row type="flex" :gutter="16" class="de_cont">
					<Col span="6">
					<p>商标价格:<br/> {{products.price}}</p>
					</Col>
					<Col span="6">
					<p>服务费:<br/> {{servicePrice}}</p>
					</Col>
					<Col span="6">
					<p>申请号:<br/> {{products.applyNo}}</p>
					</Col>
					<Col span="6">
					<p>专利类型: <br/>{{products.categoryFir}}</p>
					</Col>
				</Row>

				<p class="line"></p>
				<Row type="flex" :gutter="16" class="de_cont">
					<Col span="12">
					<p>申请时间: <br/>{{products.createTime}}</p>
					</Col>
					<Col span="12">
					<p>交易类型: <br/>{{products.transactionType}}</p>
					</Col>

					<Col span="12">
					<p>行业分类:<br/>{{products.categorySec}}</p>
					</Col>
					<Col span="12">
					<p>编号: <br/>{{products.id}}</p>
					</Col>
					<Button type="primary" shape="circle">联系方式:{{products.customerMobile}}</Button>
				</Row>
				<p class="line"></p>
				<img src="../../assets/bug1.png" class="img1" />
				<div class="de_cont2">
					<span class="title">买专利你还在担心什么？</span>
					<img src="../../assets/banner1.png" class="img2" />
					<img src="../../assets/banner2.png" class="img3" />
					<img src="../../assets/banner3.png" class="img4" />
				</div>

				<Row type="flex" :gutter="16" class="de_cont3">
					<Col span="8">
					<p class="de_box">发明型专利<span class="blue"> ¥{{servicePrice}}</span></p>
					</Col>
					<Col span="8">
					<p class="de_box">实用型专利<span class="blue"> ¥{{servicePrice}}</span></p>
					</Col>
					<Col span="8">
					<p class="de_box">外观型专利<span class="blue"> ¥{{servicePrice}}</span></p>
					</Col>
				</Row>
			</div>
		</Content>
	</div>
</template>

<script>
	import buyservice from './../public/buyservice.vue';
  import topHeader from './../public/top.vue';
  import topMeau from './../public/top_meau.vue';
	import {
		patentgetById,
	} from '../../api/api';
	export default {
		name: 'zl_details',
		data() {
			return {
				name: '',
				bannerUrl: '',
				price: '',
				servicePrice:sessionStorage.patent_fee,
				categoryFir: '',
				id: '',
				applyNo: '',
				categorySec: '',
				createTime: '',
				transactionType: '',
				categorySec: '',
				customerMobile: '',
				products: [],

			}
		},
		components: {
			buyservice,
      topHeader,
      topMeau
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//获取产品列表
			getProducts() {
				this.listLoading = true;
				//NProgress.start();
				let applyType = this.$route.query.applyType;
				let para = {
					id: this.$route.query.id,
				};
				patentgetById(para).then((res) => {
					this.products = res.data.result;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
		},
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.img {
		width: 100%;
		height: 200px;
		display: block;
	}

  .img1,
  .img2,
	.img3,
	.img4 {
		margin: 10px 0 0 0;
		width: 100%;
	}

	.top {
		margin-top: 200px;
	}

	.hm_product_num {
    position: relative;
		width: 1200px;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
    .buyseve{
      width: 220px;
      position: absolute;
      right: 20px; top: 20px;
      padding: 10px 20px;
      background-color: #ffffff;
      border-radius: 30px
    }
		overflow: hidden;
		.de_cont {
			padding: 20px;
		}
		.de_cont3 {
			padding: 0 20px 20px 20px;
			.de_box {
				font-size: 18px;
				color: #000000;
				text-align: center;
				line-height: 25px;
				background: #FFFFFF;
				font-weight: bold;
				box-shadow: 0 0 6px 0 rgba(162, 162, 162, 0.50);
				border-radius: 3px;
        padding: 20px 0;
				display: block;
				width: 95%;
				margin: 0 5%;
				.blue {
					color: #3033F6;
					text-align: center;
				}
			}
		}
		span {
			display: block;
			text-align: left;
		}
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			width: 50%;
			text-align: left;
			padding: 7px;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
		.de_cont2 {
			padding: 20px;
			.title {
				font-size: 28px;
				color: #000000;
				font-weight: bold;
				height: 45px;
				line-height: 45px;
				text-align: center;
			}
			.smal-title {
				font-size: 13px;
				color: #9B9B9B;
				text-align: center;
			}
			.title2 {
				font-size: 18px;
				color: #2B2EF5;
				line-height: 18px;
				font-weight: bold;
				margin-top: 25px;
				margin-bottom: 8px;
			}
			span {
				font-size: 14px;
				color: #000000;
				line-height: 20px;
			}
		}
	}
</style>



